import Joi from 'joi'
import chalk from 'chalk'

const defaultValue = 'server.js'
let propType = {}

const isIsomorphic = process.env.ENABLED_ISOMORPHIC === 'true'

if (isIsomorphic) {
	propType = {
		entryServer: Joi.string().trim().min(6).required()
	}
} else {
	propType = {
		entryServer: Joi.string().trim().min(6)
	}
}

export default (entryServer, getDefaultValue = false) => {
	if (getDefaultValue) {
		return defaultValue
	}
	const {error, value} = Joi.validate({entryServer}, propType)
	if (error !== null) {
		console.error(error.message)
		console.log(chalk.bold.yellow(`Current value: "${entryServer}"`))
		console.log(chalk.bold.cyan(`We are using: "${defaultValue}"`))
		return defaultValue
	}
	return value.entryServer
}